export default function Home() {
  const [userRole, setUserRole] = useState<string | null>(null)
  const [showLanding, setShowLanding] = useState(true)

  const handleEnterFromLanding = () => {
    setShowLanding(false)
  }

  const handleLoginSuccess = (role: string) => {
    setUserRole(role)
  }

  // Show landing page first
  if (showLanding) {
    return <LandingPage onEnter={handleEnterFromLanding} />
  }

  // Show login form if not logged in
  if (!userRole) {
    return <LoginForm onLoginSuccess={handleLoginSuccess} />
  }

  const roles = [
    { id: "customer", label: "Customer", icon: Users, color: "from-orange-500 to-red-500" },
    { id: "staff", label: "Staff", icon: Users, color: "from-blue-500 to-cyan-500" },
    { id: "kitchen", label: "Kitchen", icon: ChefHat, color: "from-green-500 to-emerald-500" },
    { id: "admin", label: "Admin", icon: BarChart3, color: "from-purple-500 to-pink-500" },
  ]

  return (
    <main className="min-h-screen bg-gradient-to-b from-background to-muted">
      {/* Navigation Header */}
      <header className="border-b border-border bg-card/50 backdrop-blur-sm">
        <div className="max-w-7xl mx-auto px-4 py-4 flex items-center justify-between">
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 bg-gradient-to-br from-primary to-secondary rounded-lg flex items-center justify-center text-primary-foreground font-bold">
              RF
            </div>
            <h1 className="text-2xl font-bold bg-gradient-to-r from-primary to-secondary bg-clip-text text-transparent">
              RestroFlow
            </h1>
          </div>
          <Button variant="outline" onClick={() => setUserRole(null)} className="text-foreground hover:bg-muted">
            Logout
          </Button>
        </div>
      </header>

      {/* Content Area */}
      <div className="max-w-7xl mx-auto px-4 py-8">
        {userRole === "customer" && <CustomerPortal />}
        {userRole === "staff" && <StaffInterface />}
        {userRole === "kitchen" && <KitchenDisplay />}
        {userRole === "admin" && <AdminDashboard />}
      </div>
    </main>
  )
}

interface Table {
  id: string;
  status: string;
  capacity: number;
}

function TablesDisplay() {
  const [tables, setTables] = useState<Table[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function fetchTables() {
      try {
        setLoading(true);
        const data = await getTablesList();
        setTables(data);
      } catch (err) {
        setError((err as Error).message);
      } finally {
        setLoading(false);
      }
    }
    fetchTables();
  }, []);

  if (loading) return <p>Loading tables...</p>;
  if (error) return <p className="text-red-500">Error: {error}</p>;

  return (
    <Card className="border-border bg-card/50">
      <CardHeader>
        <CardTitle>All Tables</CardTitle>
      </CardHeader>
      <CardContent>
        <div className="grid grid-cols-3 gap-3">
          {tables.map((table) => (
            <div
              key={table.id}
              className="p-4 border border-border rounded-lg hover:border-secondary hover:bg-muted cursor-pointer transition-all text-center"
            >
              <Users className="w-5 h-5 mx-auto mb-2 text-secondary" />
              <p className="font-semibold text-foreground">Table {table.id}</p>
              <p className="text-xs text-muted-foreground">Status: {table.status}</p>
              <p className="text-xs text-muted-foreground">Capacity: {table.capacity}</p>
            </div>
          ))}
        </div>
      </CardContent>
    </Card>
  );
}

